@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
.app__header{display:flex;justify-content:space-between;z-index:5}.app__title{font-weight:bold;font-size:28px;line-height:37px}.app__title span{color:#9f0013}.app__menu ul{display:flex;justify-content:space-between;font-weight:bold;font-size:24px;line-height:32px}.app__menu ul li{margin:0 8px}.app__menu ul li a:hover{color:#9f0013}.app__line{display:none;margin-top:10px}.app__burger{display:none;position:relative;width:30px;height:20px}.app__burger:before,.app__burger:after{content:"";background-color:#000;position:absolute;width:100%;height:2px;left:0;transition:all .3s ease 0s}.app__burger:before{top:0}.app__burger:after{bottom:0}.app__burger span{position:absolute;background-color:#000;left:0;width:100%;height:2px;top:9px;transition:all .3s ease 0s}.app__burger_active:before{transform:rotate(-45deg);top:9px}.app__burger_active:after{transform:rotate(45deg);bottom:9px}.app__burger_active span{transform:scale(0)}
@media(max-width: 767px){.app__header{align-items:center;flex-wrap:wrap;height:37px}.app__title{font-size:24px}.app__menu{margin-top:0;display:none}.app__menu ul{justify-content:center}.app__burger{display:block}}
.button{width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;transition:.3s transform;border:none;background-color:transparent;cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;background-color:#9f0013;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;transform:rotate(180deg);transition:none}.button__main:hover,.button__secondary:hover{color:#fff}.button__secondary .inner{background-color:#5c5c5c}.button__secondary .inner::before{border-color:#5c5c5c transparent;transition:none}.button__secondary .inner::after{border-color:#5c5c5c transparent;transition:none}.button::before{content:"";display:block;height:10px;margin-left:10px;transition:none}.button::after{content:"";display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#9f0013}.button.button__secondary::before,.button.button__secondary::after{background-color:#5c5c5c}.button:hover{transform:translateY(-5px)}.button:disabled{filter:grayscale(0.5)}*{font-family:"Roboto Condensed",sans-serif;margin:0;padding:0;box-sizing:border-box}ul,li,dl{list-style-type:none;margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0}a{text-decoration:none;color:inherit;display:inline-block}a:hover{text-decoration:none;color:inherit}p{margin:0;padding:0}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.app{max-width:1100px;margin:0 auto;padding:50px 0 50px 0;position:relative}.app .bg-decoration{position:absolute;right:-174px;bottom:-70px;z-index:-1}main{margin-top:50px;position:relative}.pulse{animation:pulse 1.5s ease-in-out .5s infinite}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}.fadeIn{animation:fadeIn ease .8s}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@media(max-width: 1480px){.bg-decoration{display:none}}@media(max-width: 1100px){.app{max-width:970px}.button,a,li{-webkit-tap-highlight-color:transparent}.button:hover{transform:translateY(0px)}}@media(max-width: 992px){.app{max-width:750px}}@media(max-width: 767px){.app{max-width:none;padding:20px 10px}main{margin-top:25px}}
